import React, { useEffect, useState, useRef } from "react";
import {
  GetOverHitSvg,
  GetIncreaseSvg,
  GetStabilitySvg,
  GetPeerSvg,
  getFreezeSvg,
  GetThermometerCtsSvg,
} from "./../img/SvgStore";
import Chart from "chart.js/auto";
import { isCompositeComponent } from "react-dom/test-utils";
import { ResizeObserver } from "@juggle/resize-observer";

export const STEPTEMPER = [
  {
    tit: "",
    cts: " ,   .",
    img: getFreezeSvg(),
    color: "#83B3DE",
    bgColor: "#E3EEF8",
    temp: 0,
  },
  {
    tit: "",
    cts: "       .",
    img: GetPeerSvg(),
    color: "#84D2D2",
    bgColor: "#E2F3F30",
    temp: 1,
  },
  {
    tit: "",
    cts: "    !",
    img: GetStabilitySvg(),
    color: "#AED292",
    bgColor: "#F2FBEB",
    temp: 2,
  },
  {
    tit: "",
    cts: " .   !",
    img: GetIncreaseSvg(),
    color: "#F6A576",
    bgColor: "#FDF0E9",
    temp: 3,
  },
  {
    tit: "",
    cts: " ,   .",
    img: GetOverHitSvg(),
    color: "#F28B8B",
    bgColor: "#FFF0F0",
    temp: 4,
  },
  {
    tit: "",
    cts: "",
    img: "",
    color: "#fff",
    bgColor: "",
    temp: 5,
  },
];

export const Temper = ({
  temp,
  tempCon,
  smtintNum,
  DataReset,
  openPopup,
  Bar,
  chartColor,
}) => {
  //   ..    ...!!!!!!!
  const [day, setDay] = useState(5);
  const [week, setWeek] = useState(5);
  const [month, setMonth] = useState(5);
  const temCon = [day, week, month];

  const chartRef = useRef();

  useEffect(() => {
    DataReset(tempCon[0], setDay);
    DataReset(tempCon[1], setWeek);
    DataReset(tempCon[2], setMonth);
  }, [tempCon]);

  const [updateData, setUpData] = useState(0);

  const ro = new ResizeObserver((entries, observer) => {
    entries.forEach((entry, index) => {
      const { width, height } = entry.contentRect;
      console.log(`Element ${index + 1}:`, `${width}x${height}`);
    });
    observer.disconnect(); // Stop observing
  });
  
  ro.observe(document.body);

  useEffect(() => {
    setUpData(smtintNum);
  }, [smtintNum]);

  // useEffect(() => {
  //   console.log(chartRef);
  //   if( chartRef !== undefined) {
  //     chartRef.current?.resize();
  //   }
  // }, [chartRef]);

  // chart Data
  let tempData = {
    labels: [" "],
    datasets: [
      {
        yAxisID: "TEMPY",
        xAxisID: "TEMPX",
        // type: "bar",
        barThickness: 25,
        data: [updateData],
        backgroundColor: chartColor,
        //    
        borderRadius: Number.MAX_VALUE,
        borderSkipped: false,

        //      X
        hoverBackgroundColor: chartColor,
      },
    ],
  };

  // chart Option
  let tempOption = {
    responsive: false,
    maintainAspectRatio: false,

    plugins: {
      legend: {
        display: false,
      },
      tooltip: null,
    },

    scales: {
      TEMPX: {
        axis: "x",
        display: false,
      },
      TEMPY: {
        axis: "y",
        max: 100,
        position: "right",
        beginAtZero: true,
        // display: false,
        grid: {
          display: false,
          color: "#eeeeee",
          fontColor: "#eeeeee",
          fontSize: 12,
        },
        gridLines: [
          {
            display: true,
            drawBorder: true,
            drawTicks: true,
            zeroLineWidth: 0,
            zeroLineColor: "#000000",
            borderColor: "#ffffff",
            lineWidth: 0,
          },
        ],
        ticks: {
          callback: function (value) {
            if (!(value % 20)) return value + " ";
            else return undefined;
          },
        },
        border: {
          display: false,
        },
      },
    },

    annotation: {
      drawTime: "afterDatasetsDraw", // (default)
      events: ["click"],
      dblClickSpeed: 350, // ms (default)
      annotations: [
        {
          drawTime: "afterDraw",
          id: "a-bar-1",
          type: "bar",
          mode: "horizontal",
          scaleID: "y-axis-0",
          value: "25",
          borderWidth: 2,
        },
      ],
    },

    animation: {
      duration: 1000,
      easing: "easeInOutSine",
      transitions: [
        {
          active: 400,
          resize: 0,
        },
      ],
    },
  };

  let tempPlugins = {
    // id: "customCanvasBackgroundColor",
    // beforeDraw: (chart, options) => {
    //   const { ctx } = chart;
    //   ctx.save();
    //   ctx.globalCompositeOperation = "destination-over";

    //   ctx.fillStyle = "#EEEEEE";
    //   // console.log(ctx);
    //   // if (ctx.roundRect !== undefined) {
    //   //   ctx.roundRect(0, 0, chart.width / 2, chart.height, 100);
    //   // }
    //   // else if (ctx.roundedRect !== undefined) {
    //   //   ctx.roundedRect(0, 0, chart.width / 2, chart.height, 100);
    //   // }
    //   ctx.beginPath();
    //   ctx.strokeStyle = "#EEEEEE";
    //   ctx.moveTo(0, 20);
    //   //ctx.arcTo(10, 0, (chart.width/2)/2, 0, 30);
    //   ctx.arc(0, 0, Math.PI, chart.width/2, 20);
    //   ctx.stroke();
    //   ctx.fill();
    //   // ctx.stroke();
    //   // ctx.fillRect(-10, -10, chart.width / 1.5, chart.height + 20);
    //   ctx.restore();
    // },
    beforeInit: (a, b, c) => {
      // const div = document.querySelector('.chartWrap');
      // a.height = div?.scrollHeight;
      // a.width = div?.scrollWidth;
    },
    // beforePrint: (a, b, c) => {
    //   console.log(a);
    // },
    resize: (a, b, c) => {
      console.log(a);
    }
  };

  return (
    <div className="temper">
      <>
        <div
          className="tempCts"
          style={{ background: STEPTEMPER[temp].bgColor }}
        >
          <p className="img">{STEPTEMPER[temp].img}</p>
          {STEPTEMPER[temp] == 5 ? null : (
            <div>
              <h3>{STEPTEMPER[temp].tit} </h3>
              <p>{STEPTEMPER[temp].cts}</p>
            </div>
          )}
        </div>

        <div className="temp">
          <div className="flex">
            <div className="tempNum">
              <div>
                {STEPTEMPER[temp] == 5 ? null : (
                  <h2 style={{ color: STEPTEMPER[temp].color }}>
                    {smtintNum}
                    <span className="do"></span>
                    <span className="ci">C</span>
                  </h2>
                )}

                <div className="tempCon">
                  {tempCon.map((cts, idx) => {
                    return (
                      <div className="flex" key={idx}>
                        <span>
                          {idx == 0 ? "" : idx == 1 ? "" : ""}
                        </span>
                        <div style={{ color: STEPTEMPER[temCon[idx]].color }}>
                          <h3>
                            {cts}
                            <span className="do"></span>
                            <span className="ci">C</span>
                          </h3>
                          <p>{STEPTEMPER[temCon[idx]].tit}</p>
                        </div>
                      </div>
                    );
                  })}
                </div>

                <div
                  className="titSpan spSmall"
                  onClick={() => {
                    openPopup(1);
                  }}
                >
                   : 
                </div>
              </div>
            </div>

            <div className="chartOne">
              <div className="chartWrap">
                <Bar
                  type="Bar"
                  data={tempData}
                  options={tempOption}
                  plugins={[tempPlugins]}
                  height="300px"
                  width="150px"
                />
              </div>

              <img src="./img/img_chartStep.png" />
            </div>
          </div>
        </div>
      </>
    </div>
  );
};
