import React, { useEffect, useRef, useState } from "react";

import styled from "styled-components";
import "./../../style/hana.css";

import PopupStore from "./../../Store/PopupStore";
import { POPUP } from "../../components/Popup";

// section1 _ components
import Title from "../../components/Title";
import { Temper, STEPTEMPER } from "../../components/Temper";

//section2
import Notice from "../../components/Notice";
import { getChartTempEx } from "./../../img/SvgStore";

import useAsync from "../../api/useAsync";
import { getSmartHome } from "../../api/smart.home.js";
import { getSmartChart } from "../../api/smart.chart.js";
import Loading from "./../../components/Loading";

import useResizeObserver from "@react-hook/resize-observer";

import { Chart, BarElement, LineElement, Tooltip, Legend, scales } from "chart.js/auto";
import { Bar, Line } from "react-chartjs-2";

const useSize = (target) => {
  const [size, setSize] = React.useState();

  React.useLayoutEffect(() => {
    setSize(target.current.getBoundingClientRect())
  }, [target]);

  // Where the magic happens
  useResizeObserver(target, (entry) => setSize(entry.contentRect));
  return size;
}


function HanaMn() {
  const { popup, openPopup, closePopup, poptxt } = PopupStore((state) => state);
  Chart.register(BarElement, LineElement, Tooltip, Legend, scales);
  Chart.defaults.font.size = 10.5;
  Chart.defaults.font.family = "Noto Sans KR";

  const target = React.useRef(null);
  const size = useSize(target);

  window.addEventListener('error', function(e) {
    console.log(e);
  });

  //    ->     : api &  1~2 
  const [state] = useAsync(() => getSmartHome("1", "2", ""), [], false);
  const [stateChart] = useAsync(() => getSmartChart("1", "2", ""), [], false);
  
  const smtintNum = Number(state.data?.smt_idx);
  const bef_day = state.data?.bef_day;
  const bef_week = state.data?.bef_week;
  const bef_month = state.data?.bef_month;
  const workdttm = state.data?.workdttm;

  const [temp, setTemp] = useState(5);
  const tempCon = [bef_day, bef_week, bef_month];
  let chartColor = STEPTEMPER[temp].color;

  useEffect(() => {
    if (state.data != null) {
      DataReset(smtintNum, setTemp);
    }
  }, [state.data]);

  const DataReset = (data, hole) => {
    if (data >= 0 && data <= 20) {
      hole(0);
    } else if (data >= 21 && data <= 40) {
      hole(1);
    } else if (data >= 41 && data <= 60) {
      hole(2);
    } else if (data >= 61 && data <= 80) {
      hole(3);
    } else if (data >= 81 && data <= 100) {
      hole(4);
    }
  };

  //   chart
  //   
  const [snpData, setSnpData] = useState();
  const [kospiData, setKospiData] = useState();
  const [jisuData, setJisuData] = useState();
  const [xAxisData, setxAxisData] = useState();
  const [smartTempData, setSmartTempdata] = useState();
  const [jisuLabel, setJisuLabel] = useState();
  const [smartLabel, setSmartLabel] = useState();

  //     
  function updateSmartChart() {
    const monthEng = [
      "Jan",
      "Feb",
      "Mar",
      "Apr",
      "May",
      "Jun",
      "Jul",
      "Aug",
      "Sep",
      "Oct",
      "Nov",
      "Dec",
    ];

    const kospi = [];
    const snp = [];
    const smartTemp = [];
    const xAxis = [];

    // eslint-disable-next-line no-extend-native
    String.prototype.insertAt = function (index, str) {
      return this.slice(0, index) + str + this.slice(index);
    };

    if (xAxis.length > 0) return;

    const years = [];
    const months = [];

    let idx = 0;

    // eslint-disable-next-line array-callback-return
    stateChart.data.map((src, index) => {
      smartTemp.push(src.smt);
      snp.push(src.snp);
      kospi.push(src.kse);

      const dateConv = src.date.insertAt(6, "-").insertAt(4, "-");

      const date = new Date(dateConv);
      const year = date.getFullYear();
      const month = date.getMonth();

      idx += 1;
      index += 1;

      if (!years.includes(year)) {
        if (!(years.length > 0 && idx < 15)) {
          const display = monthEng[month] + " " + year;
          xAxis.push({ data: dateConv, display: display });
        } else {
          xAxis.push({ data: dateConv, display: "" });
        }

        years.push(year);
        months.push(month);
        months.length = 0;

        idx = 0;
      } else if (
        index >= stateChart.data.length ||
        (!months.includes(month) && month % 3 === 0)
      ) {
        if (!(years.length > 0 && idx < 15)) {
          xAxis.push({ data: dateConv, display: monthEng[month] });
        } else {
          xAxis.push({ data: dateConv, display: "" });
        }

        months.push(month);

        idx = 0;
      } else {
        xAxis.push({ data: dateConv, display: "" });
      }
    });

    //    
    setSnpData(snp);
    setKospiData(kospi);

    setJisuData(snp);
    setJisuLabel("S&P 500");
    setSmartLabel("SMART Index");
    setSmartTempdata(smartTemp);
    setxAxisData(xAxis);
  }

  useEffect(() => {
    if (stateChart.data !== null) {
      updateSmartChart();
    }
  }, [stateChart.data]);

  const chartDataLabel = {
    labels: xAxisData,
    datasets: [
      {
        label: jisuLabel,
        data: jisuData,
        fill: false,
        yAxisID: "JISU",
        borderColor: "rgba(60,60,60,0.3)",
        borderJoinStyle: "round",
        borderWidth: 0.8,
        pointRadius: 0,
        pointHoverRadius: 2,
        pointHitRadius: 5,
      },
      {
        label: smartLabel,
        data: smartTempData,
        fill: false,
        yAxisID: "SMART",
        borderColor: chartColor,
        borderJoinStyle: "round",
        borderWidth: 1.3,
        pointRadius: 0,
        pointHoverRadius: 2,
        pointHitRadius: 5,
      },
    ],
  };

  const chartOption = {
    maintainAspectRatio: false,
    plugins: {
      tooltip: {
        usePointStyle: true,
        displayColors: true,
        callbacks: {
          title: function(tooltip) {
            if (xAxisData.length > 0) {
              return xAxisData[tooltip[0].dataIndex].data;
            }

            return tooltip.label;
          },
        }
      },
      legend: {
        align: "start",
        display: true,
        usePointStyle: true,
        onClick: undefined,
        labels: {
          boxWidth: 3,
          boxHeight: 6,
          usePointStyle: true,
          pointStyle: "circle",
          padding: 8,
          generateLabels: function (chart) {
            const labels =
              Chart.defaults.plugins.legend.labels.generateLabels(chart);
            return labels.map((property) => {
              return { ...property, fillStyle: property.strokeStyle };
            });
          },
        },
      },
    },
    scales: {
      x: {
        ticks: {
          minRotation: 30, // x      .
          autoSkip: false,
          callback: function (val, index, ticks) {
            if (xAxisData !== undefined && xAxisData.length > 0) {
              if (xAxisData[index].display !== "")
                return xAxisData[index].display;
            }

            return undefined;
          },
          // fonts: {
          //   size: 10,
          // }
        },
      },
      JISU: {
        type: "linear",
        position: "left",
        gridLines: false,
        ticks: {
          padding: 5,
          fontColor: "#323232",
        },
        grid: {
          display: false,
        },
      },
      SMART: {
        type: "linear",
        position: "right",
        display: "auto",
        gridLines: false,
        minLabelPosition: 0,
        maxLabelPosition: 100,
        ticks: {
          padding: 5,
          fontColor: "#323232",
          callback: function (value, index, values) {
            if (!(value % 20)) return value + " ";
            else return undefined;
          },
        },
      },
    },
    annotation: {
      drawTime: "afterDatasetsDraw", // (default)
      events: ["click"],
      dblClickSpeed: 350, // ms (default)
    },
    animation: {
      duration: 1000,
      easing: "easeInOutSine",
      delay: 2,
      // loop: true,
      transitions: [
        {
          resize: 0,
        },
      ],
    },
  };

  const chartPlugins = {
    afterLayout: (chart) => {
      let ctx = chart.ctx;

      ctx.save();

      let yAxis = chart.scales.SMART;

      // 

      let gradient = ctx.createLinearGradient(0, yAxis.top, 0, yAxis.bottom);

      gradient.addColorStop(0.2, STEPTEMPER[4].color);
      gradient.addColorStop(0.4, STEPTEMPER[3].color);
      gradient.addColorStop(0.6, STEPTEMPER[2].color);
      gradient.addColorStop(0.8, STEPTEMPER[1].color);
      gradient.addColorStop(1.0, STEPTEMPER[0].color);

      chart.data.datasets[1].borderColor = gradient;

      ctx.restore();
    },

    beforeInit: function (chart) {
      const originalFit = chart.legend.fit;

      chart.legend.fit = function fit() {
        originalFit.bind(chart.legend)();

        this.height += 10;
      };
    },
  };

  const selectSnp = (e) => {
    setJisuData(snpData);
    setJisuLabel("S&P 500");
  };

  const selectKospi = (e) => {
    setJisuData(kospiData);
    setJisuLabel("KOSPI");
  };

  return (
    <>
      <pre ref={target}>
        {/* {JSON.stringify({width: size.width, height: size.height}, null, 2)} */}
      </pre>

      {/*  */}
      {popup === true ? (
        <POPUP closePopup={closePopup} poptxt={poptxt} popup={popup} />
      ) : null}

      <div
        className="main"
        style={
          popup === true
            ? { position: "fixed", overflow: "hidden", touchAction: "none" }
            : null
        }
      >
        {/* 1 */}
        <section className="FirSec">
          <Title idx={0} workdttm={workdttm} />

          <div className="contour">
            <img src="./img/Hana_crt1.webp" alt=" " />
          </div>

          <Temper
            temp={temp}
            tempCon={tempCon}
            DataReset={DataReset}
            smtintNum={smtintNum}
            openPopup={openPopup}
            Bar={Bar}
            chartColor={chartColor}
          />

          <div className="fullPop">
            <button
              onClick={() => {
                openPopup(0);
              }}
            >
                 !
            </button>
          </div>
        </section>

        {/* 2 */}
        <section>
          <Title idx={1} />

          <div className="contour">
            <img src="./img/Hana_crt2.webp" alt=" " />
          </div>

          <section className="two_s_Sec">
            {/* <div className="SelectBox" onChange={changedCombo}> */}
            <div className="SelectBox">
              <input
                type="radio"
                name="select"
                id="snp"
                value="snp"
                defaultChecked
                onChange={selectSnp}
              />
              <label htmlFor="snp">S&#38;P500</label>

              <input
                type="radio"
                name="select"
                id="kospi"
                value="kospi"
                onChange={selectKospi}
              />
              <label htmlFor="kospi">KOSPI</label>
            </div>

            <div className="smart">
              <div style={{ position: "relative", height: "200px", width: "80vw" }}>
                <Line
                  type="line"
                  data={chartDataLabel}
                  options={chartOption}
                  plugins={[chartPlugins]}
                />
              </div>
              <img src="./img/img_chartTemp.svg" alt="" />
            </div>

            <p>
              AI      Risk
              Management System(RMS)    
              (SMART Index)   .
            </p>
            <br />
            <p>
                  
              (---- 5)  
                 !
            </p>
            <br />
            <p>
              5      
              ,     
                .
            </p>
            <Notice />
          </section>
        </section>
      </div>
    </>
  );
}

export default HanaMn;
