import { popupExSvg, closeBtnSvg, logoSvg } from "./../img/SvgStore";

//  
export const POPUPCTS = [
  {
    mntit: "   ",
    tit1: "?",
    con1: "    .  , 85c   ,   85%   ,      .",
    con2: "     ,         .",
    tit2: " ",
    img: popupExSvg(),
    close: closeBtnSvg(),
  },
  {
    mntit: "  ",
    con1: "   AI  , !       .",
    con2: " /         .",
    con3: "       .",
    img: logoSvg(),
  },
];

//   
export const POPUP = ({ closePopup, poptxt }) => {
  const Contxt = (poptxt) => {
    const con = POPUPCTS[poptxt];

    switch (poptxt) {
      case 0:
        return (
          <div className="fulllPopup">
            <div className="flex">
              <div
                onClick={() => {
                  closePopup();
                }}
              >
                {closeBtnSvg()}
              </div>
              <h2>{con.mntit}</h2>
            </div>

            <div className="fullPop">
              <button
                onClick={() => {
                  closePopup();
                }}
              >
                
              </button>
            </div>

            <div className="popcts">
              <div>
                <div>
                  <h4>{con.tit1}</h4>
                  <p>{con.con1}</p>
                  <br />
                  <p>{con.con2}</p>
                </div>
                <div className="contour" />
                <div>
                  <h4>{con.tit2}</h4>
                  <p className="img">{con.img}</p>
                </div>
              </div>
            </div>
          </div>
        );
      case 1:
        return (
          <div className="layerPopup">
            <div className="layerWrap">
              <div className="flex">
                <div
                  onClick={() => {
                    closePopup();
                  }}
                >
                  {closeBtnSvg()}
                </div>
                <h2>{con.mntit}</h2>
              </div>

              <div className="popcts">
                <p>{con.con1}</p>
                <br />
                <p>{con.con2}</p>
                <br />
                <p>{con.con3}</p>
                <br />
                <p className="logo">{con.img}</p>
              </div>
            </div>
          </div>
        );
    }
  };

  return (
    <div className="popup">
      <div>{Contxt(poptxt)}</div>
    </div>
  );
};
