import { Route, Routes } from "react-router-dom";

import Layout from "./pages/Hana/Layout/Layout";
import HanaMn from "./pages/Hana/HanaMn";
import Hana from "./pages/Hana/Hana";

import Main from "./pages/Main";

function App() {
  return (
    <div className="App">
      <Routes>
        <Route path="/" element={<Main />} />

        <Route element={<Layout />}>
          <Route path="hanabank" element={<HanaMn />} />
          <Route path="hana" element={<Hana />} />
        </Route>
      </Routes>
    </div>
  );
}

export default App;
